export default {
	data() {
		return {
			title: 'Hello',
			captchaImg: '',
			iphone:'',
			passwd:'',
			seconds: 120,
			inputcode:'',//用户输入的验证码
			code: '',//接口返回的验证码
			codeBtn: {
				text: '获取验证码',
				waitingCode: false,
				count: this.seconds
			},
			changeSuccess: false
		}
	},
	onLoad() {

	},
	methods: {
		sendCode: function() {
			let t = this;
			this.codeBtn.waitingCode = true;
			this.codeBtn.count = this.seconds;
			this.codeBtn.text = this.codeBtn.count + 's';

			let countdown = setInterval(() => {
				this.codeBtn.count--;
				this.codeBtn.text = this.codeBtn.count + 's';
				if (this.codeBtn.count < 0) {
					clearInterval(countdown);
					this.codeBtn.text = '重新发送';
					this.codeBtn.waitingCode = false;
				}
			}, 1000);
			uni.request({
				url: this.apiServer + '/staff/staff/sendmsg',
				method: 'POST',
				data: {
					mobile: this.iphone,
				},
				success: res => {
					console.log(res);
					if (res.data.err == 0) {
						t.code = res.data.code;
						console.log(this.code);
						uni.showToast({
							title: '短信发送成功'
						});
					}
				}
			});
		},
		forgetPasswd:function(){
			console.log(this.inputcode);
			if(this.inputcode==this.code){
				uni.request({
					url: this.apiServer+'/staff/staff/forget',
					method: 'POST',
					data: {
						iphone:this.iphone,
						passwd:this.passwd
					},
					success: res => {
						if(res.data.err==0){
							uni.showToast({
								title: '密码修改成功'
							});
							uni.redirectTo({
								url: '/pages/index/login',
								success: res => {}
							});
						}else{
							uni.showToast({
								title: res.data.msg
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}else{
				uni.showToast({
					title: '验证码输入错误'
				});
			}
			console.log(this.code)
		}
	},
	computed: {
		disableCodeBtn: function() {
			return this.codeBtn.waitingCode || this.iphone.length < 11;
		}
	}
}
